// Burger.js
import React from 'react';
import { StyledBurger } from './Burger.styled';
import { bool } from 'prop-types';

const Burger  = ({list,  open , setOpen }) => {
  
  return (
    <StyledBurger open={open} onClick={() => setOpen(!open)}>
      <a style={{textDecorationLine: "underline", marginTop: "10px"}}>Topplista</a>
     {list.map(home => <a>{home.name}:{home.score}</a>)}
    </StyledBurger>
  )


}
Burger.propTypes = {
  open: bool.isRequired,
}

export default Burger;